'use client';

import { SessionProvider } from 'next-auth/react';
import { AlertProvider } from '@/components/common/SMAlert/AlertProvider';
import { ChiefDoctorNotifications } from '@/components/ChiefDoctorNotifications/ChiefDoctorNotifications';

export default function AdminLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <SessionProvider>
      <AlertProvider>
        <div className="min-h-screen bg-gray-50">
          {children}
          <ChiefDoctorNotifications />
        </div>
      </AlertProvider>
    </SessionProvider>
  );
}
